

.customer-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 16px;
  }
  
  .Citembar {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    height: 40px;
    align-items: center;
    font-size: 16px;
    margin-left: 16px;
    background-color: #f5f5f5;
    padding: 0 8px;
  }
  
  .Citembar th {
    padding: 8px;
    text-align: left;
  }
  
  .customer-row {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    height: 60px;
    align-items: center;
    font-size: 16px;
    margin-left: 32px;
    background-color: #ffffff;
    
  }
  .customer-row td {
    color: #000; /* Set the text color specifically for <td> elements */
  }
  
  .custom-link {
    text-decoration: none;
  }
  